module.exports = "<div id=summoners_role_view _v-8eb6d89a=\"\"><div class=intro_box _v-8eb6d89a=\"\"><h2 _v-8eb6d89a=\"\">02. THE SUMMONERS</h2><div @click=show_methods() class=implementation_tag _v-8eb6d89a=\"\"><svg width=24px height=24px viewBox=\"864 144 24 24\" class=implementation_symbol _v-8eb6d89a=\"\"><g stroke=none stroke-width=1 fill=none fill-rule=evenodd transform=\"translate(864.000000, 144.000000)\" _v-8eb6d89a=\"\"><path d=\"M11,17 L13,17 L13,11 L11,11 L11,17 L11,17 Z M12,2 C6.48,2 2,6.48 2,12 C2,17.52 6.48,22 12,22 C17.52,22 22,17.52 22,12 C22,6.48 17.52,2 12,2 L12,2 Z M12,20 C7.59,20 4,16.41 4,12 C4,7.59 7.59,4 12,4 C16.41,4 20,7.59 20,12 C20,16.41 16.41,20 12,20 L12,20 Z M11,9 L13,9 L13,7 L11,7 L11,9 L11,9 Z\" fill-opacity=0.7 fill=#2DAAB7 _v-8eb6d89a=\"\"></path></g></svg><p class=methods_tag _v-8eb6d89a=\"\">Methodology / Implementation Notes</p></div><div v-show=show_note class=implementation_box _v-8eb6d89a=\"\"><svg @click=show_methods() width=14px height=14px viewBox=\"5 5 14 14\" class=exit_icon _v-8eb6d89a=\"\"><polygon stroke=none fill=rgb(245,245,245) fill-rule=evenodd points=\"19 6.41 17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12\" _v-8eb6d89a=\"\"></polygon></svg><h4 _v-8eb6d89a=\"\">Methodology / Implementation Notes</h4><p _v-8eb6d89a=\"\">Two arbitrary calculations are in play here: 1) how is a role determined a summoner's main and 2) which champs fit each role.</p><p _v-8eb6d89a=\"\">For #1, I took every summoner's 40 most recent ranked games and if over 35% of a summoner's games had the same role, listed that role as a main (Note: summoners can have up to 2 mains).</p><p _v-8eb6d89a=\"\">For #2, I used strictly my own definition for which champ counted for which role. This was especially problematic for flex roles (i.e. Ekko Top/Mid or Trundle Support/Jungle, which I generally ended up discarding).</p><p _v-8eb6d89a=\"\">Most mastered was determined by collecting the average mastery of all summoners who've played the champ at least once.</p></div><h4 class=overview_question _v-8eb6d89a=\"\">Play around with the picker below and take a look at the most commonly mastered champs each role plays on and off role. There isn't much diversity.</h4><h2 class=role_question _v-8eb6d89a=\"\"> what champions do the mains of all roles master at<span class=role_picked_tag _v-8eb6d89a=\"\">{{picked_role}}<select v-model=picked_role _v-8eb6d89a=\"\"><option v-for=\"role in roles\" :value=role _v-8eb6d89a=\"\">{{role}}</option></select></span>?</h2></div><div class=content_box _v-8eb6d89a=\"\"><h5 class=by_role_summary _v-8eb6d89a=\"\">The champs most frequently mastered at {{picked_role}} for every role's mains</h5><div class=by_role_box _v-8eb6d89a=\"\"><div class=by_role_column _v-8eb6d89a=\"\"><p class=by_role_tag _v-8eb6d89a=\"\">Rank</p><div class=popularity_box _v-8eb6d89a=\"\"><p _v-8eb6d89a=\"\">1st</p></div><div class=popularity_box _v-8eb6d89a=\"\"><p _v-8eb6d89a=\"\">2nd</p></div><div class=popularity_box _v-8eb6d89a=\"\"><p _v-8eb6d89a=\"\">3rd</p></div><div class=popularity_box _v-8eb6d89a=\"\"><p _v-8eb6d89a=\"\">4th</p></div><div class=popularity_box _v-8eb6d89a=\"\"><p _v-8eb6d89a=\"\">5th</p></div></div><div class=by_role_column _v-8eb6d89a=\"\"><p class=by_role_tag _v-8eb6d89a=\"\">Top</p><div v-for=\"champion in top_champions[this.picked_role]\" class=champion_box _v-8eb6d89a=\"\"><img :src=get_champion_image(champion) class=champion_image _v-8eb6d89a=\"\"></div></div><div class=by_role_column _v-8eb6d89a=\"\"><p class=by_role_tag _v-8eb6d89a=\"\">Jungle</p><div v-for=\"champion in jungle_champions[this.picked_role]\" class=champion_box _v-8eb6d89a=\"\"><img :src=get_champion_image(champion) class=champion_image _v-8eb6d89a=\"\"></div></div><div class=by_role_column _v-8eb6d89a=\"\"><p class=by_role_tag _v-8eb6d89a=\"\">Mid</p><div v-for=\"champion in mid_champions[this.picked_role]\" class=champion_box _v-8eb6d89a=\"\"><img :src=get_champion_image(champion) class=champion_image _v-8eb6d89a=\"\"></div></div><div class=by_role_column _v-8eb6d89a=\"\"><p class=by_role_tag _v-8eb6d89a=\"\">Support</p><div v-for=\"champion in support_champions[this.picked_role]\" class=champion_box _v-8eb6d89a=\"\"><img :src=get_champion_image(champion) class=champion_image _v-8eb6d89a=\"\"></div></div><div class=by_role_column _v-8eb6d89a=\"\"><p class=by_role_tag _v-8eb6d89a=\"\">ADC</p><div v-for=\"champion in adc_champions[this.picked_role]\" class=champion_box _v-8eb6d89a=\"\"><img :src=get_champion_image(champion) class=champion_image _v-8eb6d89a=\"\"></div></div></div></div></div>";


